import { createWebHistory, createRouter } from "vue-router";

import Home from '@/views/IndexPage.vue';
import Contacts from '@/views/ContactPage.vue';
import About from '@/views/AboutPage.vue';
import Bookings from '@/views/BookingPage.vue';
import Services from '@/views/ServicePage.vue';


const routes = [
    {
        path:'/',
        name:'Home',
        component:Home
    },
    {
        path:'/Services',
        name:'Services',
        component: Services
    },
    {
        path:'/Contacts',
        name:'Contacts',
        component: Contacts
    },
    {
        path:'/About',
        name:'About',
        component: About
    },
    {
        path:'/Bookings',
        name:'Bookings',
        component: Bookings
    }
];

const router = createRouter({
    history : createWebHistory(),
    routes
})

export default router;