<script>
import { ref } from 'vue'
export default {
name: 'FlightTodo',

setup(props, {emit} ) {
    const todos = ref([
        {text: " Vacation To Kwalalumpo", id: 1 },
        {text: " Business Trip To Casablanca", id: 1 }
    ])
   
    const newTodo = ref('')
    const addTodo = () => {
        if (newTodo.value) {
        const id = Math.random()
        todos.value = [{ text: newTodo.value, id }, ...todos.value]
        newTodo.value = ''
        } else {
        emit('badValue')
        }
    }
    
    const deleteTodo = (id) => {
        todos.value = todos.value.filter(todo => todo.id != id)
    }
       
        return { todos, addTodo, deleteTodo, newTodo }
    }

}
    

        
  </script>
  
